.pages-header {
  padding: 60px 0;
  background: url(images/help_banner.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
}

.pages-header h1 {
  color: #fff;
  font-weight: 700;
  font-size: 50px;
}

.pages-header .breadcrumbs {
  display: flex;
  flex-wrap: wrap;
  margin: 30px 0 0 0;
  padding: 0;
}

.pages-header .breadcrumbs li {
  list-style: none;
}

.pages-header .breadcrumbs li + li {
  margin-left: 25px;
  position: relative;
}

.pages-header .breadcrumbs li + li:before {
  position: absolute;
  content: "/";
  left: -14px;
  top: 0;
}

.pages-header .breadcrumbs li a {
  color: #fff;
}

.pages-header .breadcrumbs li a span {
  margin-right: 5px;
}

.pages-header .breadcrumbs li a:hover {
  color: #252B33;
}

.pages-header .breadcrumbs li.active {
  color: #eee;
}
